@keyframes hoveranimation{0%{width:0%}100%{width:100%}}.redspan{color:#f01f4b}.header{display:grid;place-items:center;margin-top:40px}.list{list-style:none;display:flex;flex-direction:row;background:rgba(26,23,23,.3607843137);border-radius:15px;padding:10px}.list li{margin-right:25px}.list li:last-of-type{margin-right:0}.list li a:not(.active):hover::after{content:"";margin-top:4px;height:3px;border-radius:99px;background:linear-gradient(90deg, hsl(347, 87%, 53%) 0%, hsl(347, 46%, 41%) 56%);display:block;animation:hoveranimation .5s}.list li a.active{color:#ccc}.list li a.active::after{content:"";margin-top:4px;height:3px;border-radius:99px;background:linear-gradient(90deg, hsl(347, 87%, 53%) 0%, hsl(347, 46%, 41%) 56%);display:block}.list li.disabled{opacity:.5}.mobile__button{appearance:none;border:0;background:0;color:#fff;font-size:40px;float:right;display:flex;position:absolute;right:0;margin-right:7px;margin-bottom:2px}.mobile__menu{height:100%;background-color:#000;width:0%;overflow:hidden;position:absolute;left:50%;opacity:0;top:50%;transform:translate(-50%, -50%);z-index:999999}.mobile__menu__header{margin-top:30px;opacity:1;margin-right:20px}.mobile__menu__body{width:100%;display:grid;place-items:center;height:50%}.mobile__menu__body__list{list-style-type:none;padding:0;width:0%}.mobile__menu__body__list li{background-color:rgba(26,24,24,.4784313725);padding:15px;margin-bottom:10px;width:0%;text-align:center}.mobile__menu__body__list li:last-of-type{margin-bottom:0}.mobile__menu__body__list li.active{background-color:rgba(52.52,48.48,48.48,.4784313725)}.mobile__menu__body__list li.active a{color:#ccc}